import { useCallback, useEffect, useState } from 'react'
import { Col, Container, Row } from 'react-bootstrap';
import DetailBtn from '../DetailBtn';
import { useT } from "../../../custom/hooks/useT";
import { BooksWorldType } from '../../../types';
import baseAPI from '../../../api/baseAPI';
import { bookUrl } from '../../../api/apiUrls';
import Loading from '../../../components/Loading';
import { Link } from "react-router-dom";

function HomeBottom() {
  const [loading, setLoading] = useState<boolean>(false)
  const { t } = useT();
  const [homeBooks, setHomeBooks] = useState<BooksWorldType>();

  const getHomeBooks = useCallback(async () => {
    setLoading(true);
    baseAPI
      .fetchWithPagination<BooksWorldType>({ url: bookUrl })
      .then(res => {
        setHomeBooks(res.data);
        setLoading(false)
      })
      .catch(error => console.error(error))
  }, [])

  useEffect(() => {
    getHomeBooks()
  }, [getHomeBooks])

  return (
    <div className="home_bottom">
      <Container >
        <Row>
          <h2 className="title mb-4 mt-3 text-black">
            {t("booksWorld")}
          </h2>
        </Row>
        <Row>
          {
            loading ? (
              <Loading height="400px" />
            ) :
              homeBooks?.results.map((book, idx) => {
                if (idx < 4) {
                  return (
                    <Col key={idx} lg={3} md={6}>
                      <Link to={`/:${book.id}`}>
                        {/* <div className="image_card">
                          <figure>
                            <img src={book.image} alt={book.title} />
                          </figure>
                        </div> */}
                        <div className="flip-box w-100 overflow-hidden">
                          <div className="flip-box-inner">
                            <div className="flip-box-front">
                              <img src={book.image} width="100%" height="100%" alt={book.title} />
                            </div>
                            <div className="flip-box-back">
                              <h4>{book.title}</h4>
                              <p>{book.description.slice(0, 56)}...</p>
                            </div>
                          </div>
                        </div>

                      </Link>
                    </Col>
                  )
                }
                return null
              }

              )
          }
        </Row>
        <Row>
          <div className="button_area">
            <DetailBtn to="/booksWorld" />
          </div>
        </Row>
      </Container>
    </div>
  )
}

export default HomeBottom